@import '~bootstrap/scss/mixins/breakpoints';


/**
  * Changing Bootstrap's default 'rem' into 'em'
 */
$font-size-base: 1em;
$headings-font-weight: bold;
$nav-link-padding-x: 1em;
$nav-link-padding-y: 0.5em;


/**
 * Custom breakpoints
 */
$grid-breakpoints: (
  xxs: 0,
  xs: 414px,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
  xxxl: 1900px,
);

$container-max-widths: (
  xs: 414px,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
  xxxl: 1900px,
);



/**
 * Adding support for text-lg-nowrap, text-sm-nowrap, etc
 */
@each $breakpoint in map-keys($grid-breakpoints) {
  .text-#{$breakpoint}-nowrap {
    @include media-breakpoint-up($breakpoint) {
      white-space: nowrap !important;
    }
  }
}
