@import 'mixins';
@import 'colors';
@import 'bubble-reactions';


.Container {
  --Container-width: 120rem;
  --Container-padding: 0;
  --Container-line-width: 0.16rem;


  max-width: var(--Container-width);
  padding: var(--Container-padding);
  margin: 0 auto;
  position: relative;

  @include mobile(){
    --Container-width: 100%;
    --Container-padding: 2rem;
  }
}

.Container__glow-1,
.Container__glow-2,
.Container__glow-3,
.Container__glow-4,
.Container__glow-5 {
  position: absolute;
  z-index: -1;
  background: #72a6ff;
  border-radius: 5rem;
  filter: blur(5rem);
}

.Container__glow-1 {
  width: 3.5rem;
  height: 43rem;
  left: 0;
  top: -8rem;
  opacity: 0.75;
}

.Container__glow-2 {
  width: 2.2rem;
  height: 43rem;
  left: 21rem;
  top: -11rem;
  opacity: 0.75;
}

.Container__glow-3 {
  width: 2.2rem;
  height: 43rem;
  left: 80.6rem;
  top: -7.6rem;
  opacity: 0.35;

  @include mobile(){
    display: none;
  }
}

.Container__glow-4 {
  width: 4.7rem;
  height: 43rem;
  left: 95rem;
  top: -8rem;
  opacity: 0.58;

  @include mobile(){
    display: none;
  }
}

.Container__glow-5 {
  width: 2.2rem;
  height: 43rem;
  right: 0;
  top: -8rem;
  opacity: 0.75;
}



/**
 * Header
 */

.Header {
  display: flex;
  margin: 3.6rem 0;

  @include mobile(){
    margin: 0.2rem 0 2rem 0;
  }
}

.Header__logo {
  width: 16.4rem;

  @include mobile(){
    width: 16rem;
    margin-left: 0.3rem;
  }
}

.Header__demo {
  margin-left: auto;
}

.Header__language {
  margin-left: 2.6rem;

  &:after {
    display: none;
  }

  @include mobile(){
    margin-left: 1rem;
    padding-left: 1.2rem;
    padding-right: 1.2rem;
    font-size: 85%;
    line-height: 2.85rem;

    & + .dropdown-menu {
      right: 0 !important;
      left: auto !important;
      transform: translate(0, 4.5rem) !important;
    }
  }
}



/**
 * Hero
 */

.Hero {
  padding: 12rem 0 22rem 0;

  &:after {
    content: '';
    position: absolute;
    top: 67rem;
    right: 42rem;
    z-index: -1;
    width: 44rem;
    height: 56rem;
    opacity: 0.26;
    background: #87E6FF;
    border-radius: 100%;
    filter: blur(5rem);
  }

  &:before {
    content: '';
    position: absolute;
    top: 100rem;
    right: 36rem;
    z-index: -1;
    width: 14.5rem;
    height: 30rem;
    opacity: 0.29;
    background: #FF40FA;
    border-radius: 100%;
    transform: rotate(23deg);
    filter: blur(5rem);
  }

  @include mobile(){
    padding: 6rem 0 12rem 0;
  }
}

.Hero__title {
  font-size: 6.3rem;
  line-height: 1.1;
  text-align: center;
  max-width: 83rem;
  margin: 0 auto;

  @include mobile(){
    font-size: 4.1rem;
    max-width: 100%;
    margin: 0;
  }
}

.Hero__graphics {
  position: relative;
  margin: 16rem 0 0 0;

  @include mobile(){
    margin: 8rem 0 0 0;
  }
}

.Hero__photo {
  --Hero__photo-width: 36.8rem;
  --Hero__photo-height: calc(var(--Hero__photo-width) * 1.448);

  width: min-content;
  position: relative;
  transform: rotate(-2deg);
  margin: 0 auto;

  @include mobile(){
    --Hero__photo-width: 60vw;
  }
}

.Hero__photo-inner {
  border: var(--Container-line-width) solid rgba($color-light, 0.3);
  background: $color-dark;
  padding: 2.1rem;
  border-radius: 1.4rem;

  @include mobile(){
    padding: 1.5rem 1.5rem 1rem 1.5rem;
  }
}

.Hero__photo-edge {
  content: '';
  background: #242424;
  border-radius: 1.4rem 1.4rem 0 0;
  border: var(--Container-line-width) solid rgba($color-light, 0.25);
  position: absolute;
  height: 2rem;

  &--front {
    top: -0.9rem;
    left: 1rem;
    right: 1rem;
    z-index: -1;
    opacity: 0.68;
  }

  &--back {
    top: -1.6rem;
    left: 2rem;
    right: 2rem;
    z-index: -2;
    opacity: 0.5;
  }
}

.Hero__photo-container {
  width: var(--Hero__photo-width);
  height: var(--Hero__photo-height);
  position: relative;
}

.Hero__photo-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  will-change: width;
  transition: width 50ms;

  &:after {
    content: '';
    position: absolute;
    inset: 0;
    opacity: 0.5;
    border: var(--Container-line-width) solid rgba($color-light, 0.3);
  }

  &--portrait {
    width: 0;
  }
}

.Hero__photo-img {
  width: var(--Hero__photo-width);
  height: var(--Hero__photo-height);

  .Hero__photo-wrapper--photo & {
    width: calc(var(--Hero__photo-width) - 0.2rem);
    height: calc(var(--Hero__photo-height) - 0.2rem);
    margin: 0.1rem 0 0 0.1rem;
  }
}

.Hero__photo-meta {
  font-size: 1.15rem;
  font-family: var(--font-familty-mono);
  margin: 1rem 0 0 0;
  line-height: 2rem;
  position: relative;
  opacity: 0.45;

  &:after {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(180deg, transparent 0%, $color-dark 95%);
  }
}

.Hero__tags {
  @include mobile(){
    display: none; // todo: display tags as tiles
  }
}

.Hero__tags-item {
  width: 19rem;
  min-height: 13.6rem;
  border: var(--Container-line-width) solid rgba($color-light, 0.3);
  background: $color-dark;
  border-radius: 1.4rem;
  font-size: 1.6rem;
  font-family: var(--font-familty-mono);
  line-height: 2.3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem;
  position: absolute;
  left: 50%;
  top: 0;

  &--single-line {
    min-height: auto;
    width: auto;
    flex-direction: row;
    padding: 1.9rem 2rem;

    .Hero__tags-item-icon {
      margin-bottom: 0;
      margin-right: 1.2rem;
    }
  }

  &--art {
    transform: translate(-38.3rem, 5.8rem)rotate(19deg);
  }

  &--engagement {
    transform: translate(-54rem, 25.5rem)rotate(10deg);
  }

  &--qr {
    transform: translate(-36rem, 38rem)rotate(-18deg);
  }

  &--hardware {
    transform: translate(-47.7rem, 63rem)rotate(3deg);
  }

  &--kiosks {
    transform: translate(23.6rem, 6rem)rotate(-14deg);
  }

  &--printing {
    transform: translate(14rem, 26rem)rotate(-6deg);
  }

  &--cloud {
    transform: translate(30rem, 40rem)rotate(3deg);
  }

  &--apps {
    transform: translate(15rem, 50.4rem)rotate(18deg);
  }
}

.Hero__tags-item-icon {
  margin-bottom: 1.4rem;
  font-size: 1.8rem;
}



/**
 * Kiosks
 */

.Kiosks {
  --Kiosks-grid-gap: 2rem;
  --Kiosks-card-height: 54rem;

  padding-bottom: 14rem;

  @include mobile(){
    padding-bottom: 7rem;
  }
}

.Kiosks__label {
  border: var(--Container-line-width) solid rgba($color-light, 0.3);
  border-radius: 0.5rem;
  padding: 0.6rem 1.5rem;
  margin-bottom: 2.7rem;
  white-space: nowrap;
  width: min-content;
}

.Kiosks__title {
  font-size: 6.3rem;
  line-height: 1.1;
  max-width: 100.6rem;
  margin: 0 0 2.7rem 0;

  @include mobile(){
    font-size: 4.1rem;
    max-width: 100%;
    margin: 0 0 1.7rem 0;
  }
}

.Kiosks__caption {
  font-size: 2.2rem;
  margin-bottom: 4.5rem;

  @include mobile(){
    font-size: 1.9rem;
    margin-bottom: 2.5rem;
  }
}

.Kiosks__cards {
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  justify-content: space-between;
}

.Kiosks__cards-col {
  display: flex;
  flex-direction: column;
  width: calc((100% - var(--Kiosks-grid-gap) - var(--Kiosks-grid-gap)) / 3);
  margin-bottom: var(--Kiosks-grid-gap);

  &--double {
    display: flex;
    justify-content: space-between;

    .Kiosks__cards-item {
      min-height: calc((var(--Kiosks-card-height) - var(--Kiosks-grid-gap)) / 2);

      @include mobile(){
        & + .Kiosks__cards-item {
          margin-top: var(--Kiosks-grid-gap);
        }
      }
    }
  }
  @include mobile(){
    width: 100%;
  }
}

.Kiosks__cards-item {
  display: flex;
  flex-direction: column;
  border-radius: 1.4rem;
  padding: 3rem;
  position: relative;
  min-height: var(--Kiosks-card-height);

  &:after {
    content: '';
    position: absolute;
    inset: 0;
    z-index: 1;
    pointer-events: none;
    border: var(--Container-line-width) solid rgba($color-light, 0.3);
    border-radius: 1.4rem;
  }

  p {
    margin: 0 0 1.5rem 0;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @include mobile(){
    padding: 2rem;
  }
}

.Kiosks__cards-item-title {
  font-size: 2.5rem;
  line-height: 3.2rem;
  margin: 0 0 1.5rem 0;
}


/* Kiosks -> Take a Photo, Generate a Portrait, Get a Printout */

.Kiosks__hardware {
  p {
    max-width: 90%;
  }
}

.Kiosks__hardware-title {
  + .Kiosks__hardware-title {
    margin-top: -1.5rem;
  }

  &:before {
    content: '';
    width: 2rem;
    height: 2rem;
    display: inline-block;
    margin-right: 1rem;
    vertical-align: middle;
    transform: translateY(-0.1rem);
    background: {
      image: url('../assets/images/icon-check-green.svg');
      position: center;
      repeat: no-repeat;
      size: contain;
    };
  }
}

.Kiosks__hardware-footer {
  margin-top: auto;
}

.Kiosks__hardware-label {
  border: var(--Container-line-width) solid rgba($color-light, 0.3);
  border-radius: 3rem;
  white-space: nowrap;
  width: min-content;
  font-size: 1.5rem;
  color: rgba($color-light, 0.45);
  padding: 0.4rem 1.2rem 0.3rem 1.2rem;
  margin-bottom: 2rem;
}

.Kiosks__hardware-list {
  font-weight: 500;
  font-variation-settings: 'wght' 500;
  margin: 0;
}

.Kiosks__hardware-list-item {
  margin-bottom: 0.6rem;

  &:last-child {
    margin-bottom: 0;
  }
}

.Kiosks__hardware-icon {
  width: 1.9rem;
  margin-right: 1rem;
  vertical-align: middle;
  transform: translateY(-0.2rem);
}


/* Kiosks -> Any Art Style â€“ From Rembrandt to Cyberpunk */

.Kiosks__style {
  overflow: hidden;
  background: {
    image: url('../assets/images/kiosk-styles-tiles.webp');
    position: center -6%;
    repeat: no-repeat;
    size: cover;
  };

  animation: Kiosks__style-tiles 60s linear infinite;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 34rem;
    background: linear-gradient(180deg, $color-dark 10%, transparent 100%);
  }

  .Kiosks__cards-item-title {
    position: relative;
  }
}

@keyframes Kiosks__style-tiles {
  from { background-position-y: 70%; }
  to { background-position-y: 0; }
}

.Kiosks__style-button {
  margin-top: auto;
  align-self: end;
  position: relative;
  z-index: 2;
  box-shadow:
    0 var(--Container-line-width) 0.3rem 0 rgba($color-dark, 0.36),
    0 1rem 7rem 0 rgba($color-dark, 0.9);
}

.Kiosks__style-link-area {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50%;
}


/* Kiosks -> Our Kiosks in Action */

.Kiosks__live {
  overflow: hidden;
  font-weight: 500;
  font-variation-settings: 'wght' 500;
  text-shadow: 0 0.2rem 0.2rem rgba($color-dark, 0.23);

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 20rem;
    opacity: 0.73;
    background-image: linear-gradient(180deg, $color-dark 22%, transparent 100%);
    z-index: -1;
  }
}

.Kiosks__live-video {
  position: absolute;
  inset: 0;
  z-index: -2;
  object-fit: cover;
  object-position: bottom;
  width: 100%;
  height: 100%;
}

.Kiosks__live-pin {
  width: 1.4rem;
  margin-right: 0.1rem;
  vertical-align: middle;
  transform: translateY(-0.2rem);
}

.Kiosks__live-more {
  margin-top: auto;
  align-self: end;
  position: relative;
  z-index: 2;
  box-shadow: 0 var(--Container-line-width) 0.3rem 0 rgba($color-dark, 0.36);
}

.Kiosks__live-link-area {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50%;
  background: none;
  border: 0;
}



/* Kiosks -> A Memorable Souvenir */

.Kiosks__souvenir {
  background: {
    image: url('../assets/images/memorable-souvenir.jpg');
    position: center;
    repeat: no-repeat;
    size: cover;
  };
}

.Kiosks__souvenir-shade {
  position: absolute;
  left: 0;
  right: 0;

  &--top {
    top: 0;
    height: 31rem;
    opacity: 0.52;
    background: linear-gradient(175deg, $color-dark, transparent 71%);
  }

  &--bottom {
    bottom: 0;
    height: 19rem;
    opacity: 0.8;
    background-image: linear-gradient(180deg, transparent 11%, $color-dark 100%);
  }
}

.Kiosks__souvenir-title {
  position: relative;
  margin-bottom: 0;
  text-shadow: 0 0.2rem 0.2rem rgba($color-dark, 0.18);
}

.Kiosks__souvenir-caption {
  position: relative;
  font-weight: 500;
  font-variation-settings: 'wght' 500;
  text-shadow: 0 0.2rem 0.2rem rgba($color-dark, 0.18);
}

.Kiosks__souvenir-footer {
  position: relative;
  margin: auto -0.3rem -0.3rem -0.3rem;
  font-size: 1.6rem;
  font-weight: 600;
  font-variation-settings: 'wght' 600;
  text-shadow: 0 0.2rem 0.2rem rgba($color-dark, 0.18);
}

.Kiosks__souvenir-reaction-count {
  min-width: 4.2rem;
  margin-right: 1.2rem;
}


/* Kiosks -> Engage Customers and Increase Revenue */

.Kiosks__engagement {
  .Kiosks__cards-item-title {
    max-width: 80%;
  }
}

.Kiosks__engagement-analytics-icon {
  width: 1.6rem;
  vert-align: middle;
  transform: translateY(-0.3rem);
  margin-right: 0.2rem;
}

.Kiosks__engagement-review {
  margin-top: auto;
}

.Kiosks__engagement-review-rating {
  width: 9.5rem;
}

.Kiosks__engagement-review-author {
  display: flex;
  color: rgba($color-light, 0.45);
  font-size: 1.5rem;
  font-weight: 500;
  font-variation-settings: 'wght' 500;
}

.Kiosks__engagement-review-author-photo {
  margin-right: 1.2rem;
  width: 4.2rem;
  border-radius: 100%;
  align-self: start;
  transform: translateY(-0.1rem);
}


/* Kiosks -> Full Installation in Two Weeks */

.Kiosks__installation {
  .Kiosks__cards-item-title {
    @include desktop(){
      margin-bottom: auto;
    }
  }
}


/* Kiosks -> Find Us Across Europe */

.Kiosks__locations-flags {
  display: flex;
  margin-top: auto;
}

.Kiosks__locations-flags-item {
  width: 3.4rem;
  margin-right: 1.4rem;
}



/**
 * Businesses
 */

.Businesses {
  padding-bottom: 12rem;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: 31rem;
    right: 44rem;
    z-index: -1;
    width: 71rem;
    height: 32rem;
    opacity: 0.14;
    background: #87E6FF;
    border-radius: 100%;
    filter: blur(5rem);
  }

  &:before {
    content: '';
    position: absolute;
    top: 40rem;
    right: 12rem;
    z-index: -1;
    width: 30rem;
    height: 39rem;
    opacity: 0.14;
    background: #FF40FA;
    border-radius: 100%;
    transform: rotate(45deg);
    filter: blur(5rem);
  }

  @include mobile(){
    padding-bottom: 8rem;
  }
}

.Businesses__title {
  font-size: 6.3rem;
  line-height: 1.1;
  text-align: center;
  max-width: 90rem;
  margin: 0 auto;

  @include mobile(){
    font-size: 4.1rem
  }
}

.Businesses__tabs {
  --duration: 0;
  --tab-gap: 1.5rem;

  margin: 4rem 0 0 0;

  @include mobile(){
    overflow-x: auto;
    width: calc(100% + (var(--Container-padding) * 2));
    margin: 2.6rem calc(var(--Container-padding) * -1) 2.2rem calc(var(--Container-padding) * -1);
    @include invisible-scrollbar();
  }
}

.Businesses__tabs-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0;

  @include mobile(){
    flex-wrap: nowrap;
    width: 100vw;
    justify-content: start;
    padding-left: calc(var(--Container-padding) - var(--tab-gap));
  }
}

.Businesses__tabs-item {
  border-radius: 3rem;
  box-shadow: inset 0 0 0 var(--Container-line-width) rgba($color-light, 0.3);
  margin: 0 calc(var(--tab-gap) / 2) var(--tab-gap) calc(var(--tab-gap) / 2);
  padding: 0.7rem 1.9rem;
  cursor: pointer;
  font-weight: 500;
  font-variation-settings: 'wght' 500;
  transition: background-color 200ms;
  position: relative;
  overflow: hidden;

  &:hover {
    background: rgba($color-light, 0.175);
  }

  &:before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(90deg, transparent, rgba($color-light, 0.15));
    transform: scaleX(0);
    transform-origin: left;
    transition-property: transform;
    transition-duration: 0ms;
  }

  &--active {
    background: $color-primary;
    box-shadow: none;
    z-index: 2;

    &:before {
      //transform: scaleX(1); // todo: fix slideshow progress bar, see BusinessesCards.js
      transition-duration: var(--duration);
    }

    &:hover {
      background: $color-primary;
    }
  }

  @include mobile(){
    min-width: 15rem;
    border-radius: 1.2rem;
    padding: 1.6rem 1.9rem 1.5rem 1.9rem;
    line-height: 1.3;
    position: relative;
    margin: 0 0 0 var(--tab-gap);
  }
}

.Businesses__tabs-item-placeholder {
  min-width: var(--Container-padding);
}

.Businesses__tabs-item-icon {
  font-size: 84%;
  vertical-align: middle;
  padding-right: 0.8rem;
  transform: translateY(-0.2rem);
  display: inline-block;

  @include mobile(){
    font-size: 110%;
    display: block;
    transform: none;
    margin-bottom: 0.6rem;
  }
}

.Businesses__cards {
  width: 100%;

  --card-width: 0.49;
  --card-active-scale: 1.2;

  --card-gap: calc(1 - (var(--card-width) * 2));                                                  // 2% on desktop
  --card-active-x-margin: calc((var(--card-width) + var(--card-gap)) * -1);                       // -51% on desktop
  --card-active-x-translate: calc(var(--card-width) + var(--card-gap) + (var(--card-gap) / 2));   // 52% on desktop
  --card-active-x-translate-value: calc(100% * var(--card-active-x-translate));
  --cards-scroll: calc(var(--Container-width) * var(--card-active-x-margin) * (var(--slide) - 1));

  @include mobile(){
    --card-width: 1;
    --card-active-scale: 1;
    --card-gap: 0;
    --card-active-x-margin: 0;
    --card-active-x-translate: 0;
    --cards-scroll: 0;
  }
}

.Businesses__cards-inner {
  display: flex;
  padding: 6.5rem 0;
  transform: translateX(var(--cards-scroll));
  margin: 0;

  @include mobile(){
    padding: 0;
  }
}

.Businesses__cards-item {
  border: var(--Container-line-width) solid rgba($color-light, 0.3);
  border-radius: 0.9rem;
  font-size: 1.7rem;
  padding: 2.4rem;
  min-height: 26rem;
  min-width: calc(100% * var(--card-width));
  margin-right: calc(100% * var(--card-gap));

  background: $color-dark;
  transition: opacity 200ms;

  &:not(.Businesses__cards-item--active) {
    opacity: 0.62;
    cursor: pointer;
    user-select: none;

    &:hover {
      opacity: 0.9;
    }

    @include mobile(){
      display: none;
    }
  }

  &:last-child {
    margin-right: 0;
  }

  &--active {
    position: relative;
    z-index: 1;
    transform: translateX(var(--card-active-x-translate-value)) scale(var(--card-active-scale));
    margin-left: calc(100% * var(--card-active-x-margin));
    transition: none;
    box-shadow:
      -8rem 0 6rem -5rem rgba($color-dark, 0.7),
      8rem 0 6rem -5rem rgba($color-dark, 0.7);
  }

  p {
    margin: 0 0 1.5rem 0;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @include mobile(){
    font-size: 1.9rem;
    padding: 3rem;
    min-height: 48rem;
    min-width: 100%;
    width: 100%;

    &--active {
      box-shadow: none;
    }
  }
}

.Businesses__cards-title {
  margin: 0 0 1.5rem 0;
  font-size: 2.4rem;

  @include mobile(){
    font-size: 2.4rem;
    line-height: 1.3;
  }
}

.Businesses__cards-icon {
  font-size: 92%;
  vertical-align: middle;
  padding-right: 0.5rem;

  @include mobile(){
    display: none;
  }
}



/**
 * Team
 */

.Team {
  display: flex;
  padding: 0 5rem 14rem 5rem;
  position: relative;
  flex-wrap: wrap;

  &:after {
    content: '';
    position: absolute;
    top: 23rem;
    right: 15rem;
    z-index: -1;
    width: 37rem;
    height: 39rem;
    opacity: 0.21;
    background: #87E6FF;
    border-radius: 100%;
    transform: rotate(3deg);
    filter: blur(5rem);
  }

  @include mobile(){
    padding: 0 0 3rem 0;
    flex-direction: column;
  }
}

.Team__title {
  font-size: 5rem;
  line-height: 1.2;

  @include mobile(){
    font-size: 4.1rem;
    order: 1;
    text-align: center;
  }
}

.Team__main {
  width: 60%;

  @include mobile(){
    width: 100%;
    order: 3;
    padding: 1.5rem 3rem 0 3rem;
  }
}

.Team__list {
  display: flex;
  flex-wrap: wrap;
  border-top: var(--Container-line-width) solid rgba($color-light, 0.22);
  margin: 3rem 0 0 0;
  padding: 3.8rem 0 0 0;

  @include mobile(){
    border: 0;
    padding: 0;
  }
}

.Team__list-item {
  width: 50%;
  color: #a2b0b3;
  font-size: 1.7rem;
  margin: 0 0 3rem 0;

  @include mobile(){
    font-size: 1.9rem;
    width: 100%;
  }
}

.Team__list-title {
  font-size: 2rem;
  margin: 0 0 0.6rem 0;
  color: $color-light;

  &:first-letter {
    text-transform: uppercase;
  }

  @include mobile(){
    line-height: 1.5;
  }
}

.Team__sidebar {
  margin-left: auto;


  @include mobile(){
    margin: 0;
    order: 2;
  }
}

.Team__facts {
  border: var(--Container-line-width) solid rgba($color-light, 0.3);
  border-radius: 1.4rem;
  padding: 4rem;
  width: 40rem;
  transform: translate(3rem, 0) rotate(3deg);

  p {
    margin-bottom: 3.1rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @include mobile(){
    width: 100%;
    padding: 3rem;
    margin-top: 2rem;
    transform: none;
  }
}

.Team__facts-title {
  font-size: 2.2rem;
  margin: 0 0 2rem 0;
}

.Team__facts-nature-logo {
  width: 9rem;
  display: block;
  margin-bottom: 0.8rem;
}

.Team__facts-facebook-logo {
  width: 13.4rem;
  display: block;
  margin-bottom: 0.8rem;
}



/**
 * Contact
 */

.Contact {
  border-radius: 1.7rem;
  margin-bottom: 12rem;
  padding: 7rem;
  position: relative;

  background: {
    color: #2C2C2C;
    image: url('../assets/images/contact-pattern-1.svg'), url('../assets/images/contact-pattern-2.svg');
    position: bottom left, top right;
    repeat: no-repeat;
    size: 22rem, 41rem;
  };

  @include mobile(){
    padding: 3rem;
    margin-bottom: 9rem;
    background-image: none;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      height: 22rem;
      transform: scaleY(-1) scaleX(-1);
      background: {
        image: url('../assets/images/contact-pattern-1.svg');
        position: bottom left;
        repeat: no-repeat;
        size: contain;
      };
    }
  }
}

.Contact__title {
  font-size: 5.6rem;
  margin: 0 0 3.8rem 0;

  @include mobile(){
    font-size: 4.1rem;
    margin: 0 0 2rem 0;
  }
}

.Contact__list {
  margin: 0 0 5.5rem 0;

  @include mobile(){
    margin-bottom: 4rem;
  }
}

.Contact__list-item {
  font-size: 2.2rem;
  margin-bottom: 1.5rem;
  font-weight: 500;
  font-variation-settings: 'wght' 500;
  padding-left: 2.5rem;
  margin-left: 0.3rem;

  background: {
    image: url('../assets/images/icon-check-white.svg');
    repeat: no-repeat;
    position: left center;
    size: 1.4rem;
  };

  &:last-child {
    margin-bottom: 0;
  }

  @include mobile(){
    font-size: 1.9rem;
  }
}

.Contact__footer {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-variation-settings: 'wght' 500;

  @include mobile(){
    flex-direction: column;
    margin-top: 0;
    border-top: var(--Container-line-width) solid rgba($color-light, 0.2);
    padding-top: 4.5rem;
  }
}

.Contact__footer-button {
  font-size: 2rem;
  padding: 1.55rem 3.5rem;
  margin-right: 2rem;

  @include mobile(){
    margin-right: 0;
  }
}

.Contact__footer-caption {
  @include mobile(){
    margin-top: 2rem;
    margin-bottom: 0.1rem;
  }
}

.Contact__footer-email {
  margin-left: 1rem;

  @include mobile(){
    margin-left: 0;
  }
}

.Contact__popup {
  --bs-modal-bg: #{$color-dark};
  --bs-modal-border-color: #{rgba($color-light, 0.3)};

  font-size: 1.9rem;
  scroll-behavior: smooth;
}

.Contact__popup-close {
  position: absolute;
  top: 2rem;
  right: 1.1rem;
  width: 3.3rem;
  font-size: 1.6rem;
}

.Contact__popup-title {
  font-size: 2.5rem;
}

.Contact__popup-button {
  font-size: 2rem;
  padding: 1.55rem 3.5rem;
  margin-bottom: 2.6rem;
}


/**
 * Footer
 */

.Footer {
  font-size: 1.7rem;
  line-height: 1.6;
  margin-bottom: 4.3rem;

  a {
    color: $color-light;
    text-decoration: none;
    transition: opacity 200ms;

    &:hover {
      opacity: 0.7;
    }
  }

  @include mobile(){
    margin-bottom: 0;
  }
}

.Footer__logo {
  width: 15.5rem;
  margin-bottom: 3rem;

  @include mobile(){
    margin-bottom: 2rem;
  }
}

.Footer__col {
  margin-left: 7rem;

  &:first-child {
    margin-left: 0;
  }

  @include mobile(){
    margin-left: 0;
    margin-top: 2rem;

    &:first-child {
      margin-top: 0;
    }
  }
}

.Footer__main {
  display: flex;
  margin-bottom: 2.6rem;

  @include mobile(){
    flex-direction: column;
  }
}

.Footer__language {
  &:after {
    display: none !important;
  }
}

.Footer__subtext {
  font-size: 1.5rem;
  opacity: 0.48;
}



/**
 * "Our Kiosks In Action" Popup
 */

.KioskLivePopup {
  --bs-modal-bg: #{$color-dark};
  --bs-modal-border-color: #{rgba($color-light, 0.3)};
  --bs-modal-width: calc(var(--Container-width) + 2rem);

  font-size: 1.9rem;
  scroll-behavior: smooth;
}

.KioskLivePopup__close {
  position: absolute;
  top: 2rem;
  right: 1.1rem;
  width: 3.3rem;
  font-size: 1.6rem;
}



/**
 * Locations & globe
 */

.Locations {
  --Locations--globe-size: calc(var(--Container-width) / 2);

  position: relative;
  display: flex;
  justify-content: end;
  padding: 5rem 5rem 16rem 5rem;

  @include mobile(){
    padding: 37.2rem 0 16rem 0;
  }
}

.Locations__globe {
  width: var(--Locations--globe-size);
  height: var(--Locations--globe-size);
  opacity: 0;
  position: relative;
  transition: opacity 1000ms;

  &.earth-ready {
    opacity: 1;
  }

  .earth-overlay {
    pointer-events: all !important;
  }

  @include mobile(){
    width: 90%;
    margin: 0 auto;
  }
}

.Locations__glow {
  position: absolute;
  inset: -2rem;
  pointer-events: none;
  border-radius: 100%;
  background: $color-primary;
  filter: blur(2rem);
  opacity: 0;
  transition: opacity 500ms;

  .earth-ready & {
    opacity: 0.3;
  }
}

.Locations__info {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  max-width: 50%;
  display: flex;
  align-items: start;
  justify-content: center;
  flex-direction: column;
  padding: 0 0 10rem 5rem;

  @include mobile(){
    max-width: 100%;
    text-align: center;
    align-items: center;
    justify-content: start;
    padding: 0;
  }

  &:after {
    content: '';
    z-index: -1;
    opacity: .17;
    filter: blur(5rem);
    background: #87e6ff;
    border-radius: 100%;
    width: 37rem;
    height: 42rem;
    position: absolute;
    top: 8rem;
    left: -6rem;
    transform: rotate(3deg);

    @include mobile(){
      width: 30rem;
      height: 40rem;
      top: -10rem;
      left: -1rem;
    }
  }
}

.Locations__title {
  font-size: 6.3rem;
  margin-bottom: 2.5rem;
  line-height: 1.1;

  @include mobile(){
    font-size: 4.1rem;
  }
}

.Locations__caption {
  font-size: 2.2rem;

  @include mobile(){
    font-size: 1.9rem;
  }
}

.Locations__counter {
  list-style: none;
  padding: 0;
  margin: 3rem 0 0 0;
  display: flex;
  gap: 3rem;

  @include mobile(){
    gap: 2.7rem;
    margin-top: 4rem;
  }
}

.Locations__counter-item {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.Locations__counter-value {
  font-size: 3.8rem;
  font-family: var(--font-familty-header);
  font-weight: 700;
  font-variation-settings: 'wght' 600;
  margin-bottom: -0.4rem;

  &:after {
    content: 'â€Ž ';
  }
}

.Locations__counter-caption {
  text-transform: uppercase;
  letter-spacing: 0.12rem;
  font-size: 1.1rem;
  font-weight: 700;
  font-variation-settings: 'wght' 650;
  opacity: 0.6;
}

.Locations__marker-pin {
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 100%;
  position: absolute;
  transform: translate(-0.9rem, -0.9rem) scale(1.2);
  transition: opacity 300ms;
  z-index: -1;
  cursor: pointer;

  &:hover {
    background: rgba(#FFFFFF, 0.4);
  }

  &:after {
    content: '';
    width: 50%;
    height: 50%;
    background: #ffffff;
    position: absolute;
    border-radius: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .active & {
    opacity: 0;
  }

  .earth-occluded & {
    opacity: 0 !important;
  }
}

.Locations__marker-balloon {
  background: $color-light;
  font-size: 1.6rem;
  color: $color-dark;
  opacity: 0;
  padding: 1.5rem 1.8rem;
  border-radius: 1.5rem;
  box-shadow: 0 2rem 3rem -2rem rgba(#000000, 0.5);
  z-index: 3;
  pointer-events: none;
  position: absolute;
  white-space: nowrap;
  transform: translate(-2.4rem, calc(-100% - 0.4rem));
  transition: opacity 1000ms;

  @include mobile(){
    padding: 1.3rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 1.2rem;
  }

  .active & {
    opacity: 1;
  }

  .earth-occluded & {
    opacity: 0 !important;
  }

  &:after {
    content: '';
    background: #ffffff;
    position: absolute;
    bottom: -0.5rem;
    left: 2rem;
    width: 1rem;
    height: 1rem;
    transform: scale(1, 1.1) rotate(45deg);

    @include mobile(){
      width: 0.7rem;
      height: 0.7rem;
      bottom: -0.3rem;
    }
  }
}

.Locations__marker-title {
  font-weight: 600;
  font-variation-settings: 'wght' 600;
}

.Locations__marker-caption {
  color: rgba($color-dark, 0.6);
}

.Locations__marker-caption-flag {
  color: $color-dark;
}

