.Reactions {
  --Reactions-bubble-size: 4rem;
  --Reactions-bubble-opacity: 0.8;
  --horizontal-spread: 5rem;
  --vertical-distance: 24rem;
  --animation-duration: 10s;

  position: relative;
}

.Reactions__static {
  position: relative;
}

.Reactions__bubble {
  position: absolute;
  bottom: 10px;
  left: 25%;
  font-size: var(--Reactions-bubble-size);
  opacity: 0;
  z-index: 5;
}

.Reactions__bubble--1 {
  animation: Reactions__bubble--1 var(--animation-duration) ease-in-out infinite;
  animation-timing-function: cubic-bezier(0.45, 0, 0.55, 1);
}

.Reactions__bubble--2 {
  animation: Reactions__bubble--2 calc(var(--animation-duration) + (var(--animation-duration) * 0.3)) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * 0.1);
  animation-timing-function: cubic-bezier(0.25, 0.1, 0.25, 1);
}

.Reactions__bubble--3 {
  animation: Reactions__bubble--3 calc(var(--animation-duration) - (var(--animation-duration) * 0.1)) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * 0.2);
  animation-timing-function: cubic-bezier(0.3, 0.2, 0.5, 0.8);
}

.Reactions__bubble--4 {
  animation: Reactions__bubble--4 calc(var(--animation-duration) + (var(--animation-duration) * 0.2)) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * 0.4);
}

.Reactions__bubble--5 {
  animation: Reactions__bubble--5 calc(var(--animation-duration) - (var(--animation-duration) * 0.2)) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * 0.6);
  animation-timing-function: cubic-bezier(0.4, 0.1, 0.4, 0.9);
}

.Reactions__bubble--6 {
  animation: Reactions__bubble--6 calc(var(--animation-duration) + (var(--animation-duration) * 0.1)) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * 0.7);
  animation-timing-function: cubic-bezier(0.6, 0.1, 0.3, 0.9);
}

.Reactions__bubble--7 {
  animation: Reactions__bubble--7 calc(var(--animation-duration) - (var(--animation-duration) * 0.07)) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * 0.9);
  animation-timing-function: cubic-bezier(0.35, 0.15, 0.65, 0.85);
}

@keyframes Reactions__bubble--1 {
  0% {
    transform: translateX(-50%) scale(0.5);
    opacity: 0;
  }
  10% {
    opacity: var(--Reactions-bubble-opacity);
  }
  100% {
    transform: translate(calc(-50% + (var(--horizontal-spread) * 0.625)), calc(-1 * var(--vertical-distance))) scale(0.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--2 {
  0% {
    transform: translateX(-50%) scale(0.5);
    opacity: 0;
  }
  10% {
    opacity: var(--Reactions-bubble-opacity);
  }
  100% {
    transform: translate(calc(-50% - (var(--horizontal-spread) * 0.875)), calc(-1 * var(--vertical-distance))) scale(0.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--3 {
  0% {
    transform: translateX(-50%) scale(0.5);
    opacity: 0;
  }
  10% {
    opacity: var(--Reactions-bubble-opacity);
  }
  100% {
    transform: translate(calc(-50% + (var(--horizontal-spread) * 0.25)), calc(-1 * var(--vertical-distance))) scale(0.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--4 {
  0% {
    transform: translateX(-50%) scale(0.5);
    opacity: 0;
  }
  10% {
    opacity: var(--Reactions-bubble-opacity);
  }
  100% {
    transform: translate(calc(-50% - (var(--horizontal-spread) * 0.375)), calc(-1 * var(--vertical-distance))) scale(0.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--5 {
  0% {
    transform: translateX(-50%) scale(0.5);
    opacity: 0;
  }
  10% {
    opacity: var(--Reactions-bubble-opacity);
  }
  100% {
    transform: translate(calc(-50% + var(--horizontal-spread)), calc(-1 * var(--vertical-distance))) scale(0.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--6 {
  0% {
    transform: translateX(-50%) scale(0.5);
    opacity: 0;
  }
  10% {
    opacity: var(--Reactions-bubble-opacity);
  }
  100% {
    transform: translate(calc(-50% - var(--horizontal-spread)), calc(-1 * var(--vertical-distance))) scale(0.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--7 {
  0% {
    transform: translateX(-50%) scale(0.5);
    opacity: 0;
  }
  10% {
    opacity: var(--Reactions-bubble-opacity);
  }
  100% {
    transform: translate(calc(-50% + (var(--horizontal-spread) * 0.125)), calc(-1 * var(--vertical-distance))) scale(0.2);
    opacity: 0;
  }
}
