.Reactions {
  --Reactions-bubble-size: 4rem;
  --Reactions-bubble-opacity: .8;
  --horizontal-spread: 5rem;
  --vertical-distance: 24rem;
  --animation-duration: 10s;
  position: relative;
}

.Reactions__static {
  position: relative;
}

.Reactions__bubble {
  font-size: var(--Reactions-bubble-size);
  opacity: 0;
  z-index: 5;
  position: absolute;
  bottom: 10px;
  left: 25%;
}

.Reactions__bubble--1 {
  animation: Reactions__bubble--1 var(--animation-duration) ease-in-out infinite;
  animation-timing-function: cubic-bezier(.45, 0, .55, 1);
}

.Reactions__bubble--2 {
  animation: Reactions__bubble--2 calc(var(--animation-duration)  + var(--animation-duration) * .3) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * .1);
  animation-timing-function: ease;
}

.Reactions__bubble--3 {
  animation: Reactions__bubble--3 calc(var(--animation-duration)  - var(--animation-duration) * .1) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * .2);
  animation-timing-function: cubic-bezier(.3, .2, .5, .8);
}

.Reactions__bubble--4 {
  animation: Reactions__bubble--4 calc(var(--animation-duration)  + var(--animation-duration) * .2) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * .4);
}

.Reactions__bubble--5 {
  animation: Reactions__bubble--5 calc(var(--animation-duration)  - var(--animation-duration) * .2) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * .6);
  animation-timing-function: cubic-bezier(.4, .1, .4, .9);
}

.Reactions__bubble--6 {
  animation: Reactions__bubble--6 calc(var(--animation-duration)  + var(--animation-duration) * .1) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * .7);
  animation-timing-function: cubic-bezier(.6, .1, .3, .9);
}

.Reactions__bubble--7 {
  animation: Reactions__bubble--7 calc(var(--animation-duration)  - var(--animation-duration) * .07) ease-in-out infinite;
  animation-delay: calc(var(--animation-duration) * .9);
  animation-timing-function: cubic-bezier(.35, .15, .65, .85);
}

@keyframes Reactions__bubble--1 {
  0% {
    opacity: 0;
    transform: translateX(-50%)scale(.5);
  }

  10% {
    opacity: var(--Reactions-bubble-opacity);
  }

  100% {
    transform: translate(calc(-50% + var(--horizontal-spread) * .625), calc(-1 * var(--vertical-distance))) scale(.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--2 {
  0% {
    opacity: 0;
    transform: translateX(-50%)scale(.5);
  }

  10% {
    opacity: var(--Reactions-bubble-opacity);
  }

  100% {
    transform: translate(calc(-50% - var(--horizontal-spread) * .875), calc(-1 * var(--vertical-distance))) scale(.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--3 {
  0% {
    opacity: 0;
    transform: translateX(-50%)scale(.5);
  }

  10% {
    opacity: var(--Reactions-bubble-opacity);
  }

  100% {
    transform: translate(calc(-50% + var(--horizontal-spread) * .25), calc(-1 * var(--vertical-distance))) scale(.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--4 {
  0% {
    opacity: 0;
    transform: translateX(-50%)scale(.5);
  }

  10% {
    opacity: var(--Reactions-bubble-opacity);
  }

  100% {
    transform: translate(calc(-50% - var(--horizontal-spread) * .375), calc(-1 * var(--vertical-distance))) scale(.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--5 {
  0% {
    opacity: 0;
    transform: translateX(-50%)scale(.5);
  }

  10% {
    opacity: var(--Reactions-bubble-opacity);
  }

  100% {
    transform: translate(calc(-50% + var(--horizontal-spread)), calc(-1 * var(--vertical-distance))) scale(.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--6 {
  0% {
    opacity: 0;
    transform: translateX(-50%)scale(.5);
  }

  10% {
    opacity: var(--Reactions-bubble-opacity);
  }

  100% {
    transform: translate(calc(-50% - var(--horizontal-spread)), calc(-1 * var(--vertical-distance))) scale(.2);
    opacity: 0;
  }
}

@keyframes Reactions__bubble--7 {
  0% {
    opacity: 0;
    transform: translateX(-50%)scale(.5);
  }

  10% {
    opacity: var(--Reactions-bubble-opacity);
  }

  100% {
    transform: translate(calc(-50% + var(--horizontal-spread) * .125), calc(-1 * var(--vertical-distance))) scale(.2);
    opacity: 0;
  }
}

